import { AuthOptions } from "next-auth";
import CredentialsProvider from "next-auth/providers/credentials";
import { AuthService } from "@/swagger/services/AuthService";
import { handleApiErrorWithCleanup } from "@/lib/api-error-handler";
import { OpenAPI } from "@/swagger/core/OpenAPI";
// Initialize OpenAPI configuration early
import "@/lib/openapi-config";

export const authOptions: AuthOptions = {
  providers: [
    CredentialsProvider({
      id: "otp-login",
      name: "OTP Login",
      credentials: {
        phoneNumber: { label: "Phone Number", type: "text" },
        code: { label: "Code", type: "text" },
        recaptchaToken: { label: "ReCAPTCHA Token", type: "text" },
      },
      async authorize(credentials) {
        if (!credentials?.phoneNumber || !credentials?.code) {
          throw new Error("ÿ¥ŸÖÿßÿ±Ÿá ÿ™ŸÑŸÅŸÜ Ÿà ⁄©ÿØ ÿ™ÿß€å€åÿØ ÿßŸÑÿ≤ÿßŸÖ€å ÿßÿ≥ÿ™");
        }

        try {
          const requestBody: any = {
            phoneNumber: credentials.phoneNumber,
            code: parseInt(credentials.code),
          };

          // Add captcha token if provided
          if (credentials.recaptchaToken) {
            requestBody.captchaToken = credentials.recaptchaToken;
          }
          const check = OpenAPI;
          const response = await AuthService.petInsurancePlatformUsersEndpointsAuthLoginByCodeEndpoint({
            requestBody,
          });

          if (response?.accessToken && response?.refreshToken && response.userId) {
            return {
              id: response.userId,
              phoneNumber: credentials.phoneNumber,
              accessToken: response.accessToken,
              refreshToken: response.refreshToken,
            } as const;
          }
          throw new Error("Ÿæÿßÿ≥ÿÆ ŸÜÿßŸÖÿπÿ™ÿ®ÿ± ÿßÿ≤ ÿ≥ÿ±Ÿàÿ±");
        } catch (error) {
          console.error("OTP login error:", error);
          const errorMessage = handleApiErrorWithCleanup(error, "⁄©ÿØ ÿ™ÿß€å€åÿØ ŸÜÿßŸÖÿπÿ™ÿ®ÿ± ÿßÿ≥ÿ™. ŸÑÿ∑ŸÅÿß ÿØŸàÿ®ÿßÿ±Ÿá ÿ™ŸÑÿßÿ¥ ⁄©ŸÜ€åÿØ.");
          throw new Error(errorMessage);
        }
      },
    }),
    CredentialsProvider({
      id: "password-login",
      name: "Password Login",
      credentials: {
        phoneNumber: { label: "Phone Number", type: "text" },
        password: { label: "Password", type: "password" },
        recaptchaToken: { label: "ReCAPTCHA Token", type: "text" },
      },
      async authorize(credentials) {
        if (!credentials?.phoneNumber || !credentials?.password) {
          console.error("‚ùå [AUTH] Missing credentials for password login");
          throw new Error("ÿ¥ŸÖÿßÿ±Ÿá ÿ™ŸÑŸÅŸÜ Ÿà ÿ±ŸÖÿ≤ ÿπÿ®Ÿàÿ± ÿßŸÑÿ≤ÿßŸÖ€å ÿßÿ≥ÿ™");
        }

        try {
          console.log("üîê [AUTH] Attempting password login for:", credentials.phoneNumber);
          
          const requestBody: any = {
            phoneNumber: credentials.phoneNumber,
            password: credentials.password,
          };

          // Add captcha token if provided
          if (credentials.recaptchaToken) {
            requestBody.captchaToken = credentials.recaptchaToken;
          }

          const check = OpenAPI;
          const response = await AuthService.petInsurancePlatformUsersEndpointsAuthLoginByPasswordEndpoint({
            requestBody,
          });

          console.log("üîê [AUTH] Login API response received");

          if (response?.accessToken && response?.refreshToken && response.userId) {
            console.log("‚úÖ [AUTH] Login successful for user:", response.userId);
            return {
              id: response.userId,
              phoneNumber: credentials.phoneNumber,
              accessToken: response.accessToken,
              refreshToken: response.refreshToken,
            } as const;
          }
          console.error("‚ùå [AUTH] Invalid response from login API:", response);
          throw new Error("Ÿæÿßÿ≥ÿÆ ŸÜÿßŸÖÿπÿ™ÿ®ÿ± ÿßÿ≤ ÿ≥ÿ±Ÿàÿ±");
        } catch (error) {
          console.error("‚ùå [AUTH] Password login API error:", error);
          const errorMessage = handleApiErrorWithCleanup(error, "ÿßÿ∑ŸÑÿßÿπÿßÿ™ Ÿàÿ±ŸàÿØ ŸÜÿßŸÖÿπÿ™ÿ®ÿ± ÿßÿ≥ÿ™. ŸÑÿ∑ŸÅÿß ÿØŸàÿ®ÿßÿ±Ÿá ÿ™ŸÑÿßÿ¥ ⁄©ŸÜ€åÿØ.");
          console.log("üîê [AUTH] Throwing error:", errorMessage);
          throw new Error(errorMessage);
        }
      },
    }),
  ],
  callbacks: {
    async jwt({ token, user }) {
      // The user object passed here contains the data returned by the authorize function
      if (user) {
        token.accessToken = user.accessToken;
        token.refreshToken = user.refreshToken;
        token.sub = user.id; // Store user ID in token
        token.id = user.id;
      }
      return token;
    },
    async session({ session, token }) {
      // The token object contains the data from the jwt callback
      if (token) {
        session.accessToken = token.accessToken;
        session.refreshToken = token.refreshToken;
        // Ensure user ID is available for refresh token operations
        if (session.user && token.sub) {
          session.user.id = token.sub;
        }
      }
      return session;
    },
    async signIn({ user, account, profile, email, credentials }) {
      // This callback is called when sign in succeeds
      console.log("üîê [AUTH CALLBACK] SignIn callback called with user:", user);
      return true; // Allow sign in
    },
  },
  events: {
    async signIn(message) {
      console.log("üîê [AUTH EVENT] Sign in event:", message);
    },
    async signOut(message) {
      console.log("üîê [AUTH EVENT] Sign out event");
      
      try {
        // Try to get token from session first, then from token
        const accessToken = message.session?.accessToken || message.token?.accessToken;
        
        if (accessToken) {
          OpenAPI.TOKEN = accessToken;
          const response = await AuthService.petInsurancePlatformUsersEndpointsAuthLogoutEndpoint();
          console.log("üîê [AUTH EVENT] Backend logout successful", response);
        } else {
          console.log("üîê [AUTH EVENT] No access token found - skipping backend logout");
        }
      } catch (error: any) {
        console.error("üîê [AUTH EVENT] Logout API error:", error);
        // Don't throw the error - logout should succeed even if API call fails
        if (error?.status === 401) {
          console.log("üîê [AUTH EVENT] Token expired/invalid - continuing with logout");
        }
      } finally {
        // Always clear the token after logout attempt
        OpenAPI.TOKEN = undefined;
      }
    },
  },
  pages: {
    signIn: "/login",
  },
  session: {
    strategy: "jwt",
    maxAge: 60 * 60, // 1 hour
    updateAge: 24 * 60 * 60, // 24 hours
  },
  secret: process.env.NEXTAUTH_SECRET,
}; 